.app-wrapper {
  margin: 0 auto;
  width: 1500px;
  display: grid;


  grid-template-areas:
'h h'
'n c';

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr

}



.app-wrapper-content {
  grid-area: c;
  background-color: blue;
}

.app-wrapper-content img {
  width: 80%;

}










